
.app__navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 150px;
    padding: 1rem 2rem;

   

}


.app__navbar-logo{
    
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.app__navbar-logo img{
    width: 180px;
    height: 180px;
}

.app__navbar-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 80px;
    list-style: none;
}



.app__navbar-links li{
    margin: 0 1rem;
    cursor: pointer;
    font-family: var(--font-base); /* */
    font-size: 27px; /* */
}

.app__navbar-links li:hover{
   color: var(--color-grey);
}

.app__navbar-order {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
}

.app__navbar-order a{
    margin: 0 1rem;
    text-decoration: none;
    transition: 0.5s ease;
    font-family: var(--font-base); /* */
    font-size: 27px;  /* */
    
}

.app__navbar-order a:hover{
    border-bottom: 1px solid var(--color-golden);
}

.app__navbar-links div {
    width: 1px;
    height: 30px;
    background-color: var(--color-grey);
}

.app__navbar-smallscreen{
    display: none;
}

.app__navbar-smallscreen_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%; /* Covers entire width */
    height: 100vh; /* Covers full height */
    background: var(--color-black);
    transition: all 0.5s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

.app__navbar-smallscreen-links {
    list-style: none;
    text-align: center;
    padding: 0;
    margin: 0;
}

.app__navbar-smallscreen-links li {
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    font-family: var(--font-base);
}

.app__navbar-smallscreen-links li:hover {
    color: var(--color-white);
}


.app__navbar-smallscreen_overlay .overlay__close{
    font-size: 27px;
    color: var(--color-golden);
    cursor: pointer;

    position: absolute;
    top: 20px;
    right: 20px;

}




@media screen and (max-width: 2000px){
    
}

@media screen and (max-width: 1150px) {
    .app__navbar-links{
        display: none;
    }

    .app__navbar-order{
        display: none;
    }
    .app__navbar-smallscreen{
        display: flex;
    }
}

@media screen and (max-width: 650px) {
   
   
    .app__navbar-order{
        display: none;
    }
    
}